* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
  font-family: inherit;
  color: inherit;
}

input,
select {
  font-family: inherit;
  outline: none;
}

body {
  font-family: "Inter";
}

.reactLink {
  text-decoration: none;
  color: inherit;
}